<template>	
<section class="wrap has_header_wrap c_wrap">
	<!-- 头部标题 -->
	<mt-header fixed title="修改昵称">
      <mt-button icon="back" slot="left" @click.prevent="goBack"></mt-button>
	  <mt-button slot="right" @click="btnSave">确定</mt-button>
	</mt-header>
	<!-- 头部标题 end -->
	
	<div class="info_change">
		<!-- 修改昵称 -->
		<mt-field placeholder="请输入昵称" v-model.tirm="username"></mt-field>
		<!-- 修改昵称 end -->
	</div>
	
</section>
</template>

<script>
import { Field, Header, Button, Toast } from 'mint-ui';

export default {
	name: 'InfoChange',
	data (){
		return {
			username: this.$route.query.name?this.$route.query.name:'', //昵称
		}
	},
	components: {
		'mt-field': Field,
		'mt-header': Header,
		'mt-button': Button,
	},
	methods: {
		//确定按钮
		btnSave (){	
			const _self = this;

			if( !this.username ){
				return false;
			}
			
			this.$options.methods.updateInfo( this, 'memberName', this.username );
			this.$emit('cName', this.username)
			this.$router.go(-1);
		},

		//公共函数
		// 后退
		goBack (){	
			this.$router.go(-1);
		},
	    //显示提示信息
	    showToast( msg, time ){
	      var time = time ? time : 2000;

	      Toast({
	        message: msg,
	        position: 'center',
	        duration: time
	      });     
	    },
	    //更新信息
		updateInfo ( _self, key, data ){
			var datas = {};

			datas.memberID = _self.$store.state.memberId;
			datas[key] = data;

			_self.$ajax({
				url: _self.webRoot + '/updateByMemberId',
				data: datas, 
			})
			.then(function(res){
				// console.log('修改： ', res)
				if( !res.data.success ){
					_self.$options.methods.showToast( res.data.msg );
					return false;
				}
				_self.$store.state.memberInfo[key] = data;
				// 更新个人信息
  				_self.$store.commit('add', {
  					addMemberInfo: _self.$store.state.memberInfo,
  				});
				_self.$options.methods.showToast( '修改成功!', 1000 );
				return true;
			})
			.catch(function(err){
				console.log(err)
				return false;
			});
		},
	},
}
</script>
<style scoped>
@import '../../../static/css/headerTitle.css';	
</style>




